import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute} from '@angular/router';
import { ToastController, LoadingController, AlertController, NavController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { Storage } from '@ionic/storage';


@Component({
  selector: 'app-verusuadmin',
  templateUrl: './verusuadmin.page.html',
  styleUrls: ['./verusuadmin.page.scss'],
})
export class VerusuadminPage implements OnInit {

  users: any = [];
  limit: number = 13
  start: number = 0;

  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private accsPrvds: AccessProviders,
    private navCtrl: NavController,
    private storage: Storage
  ) { }

  ngOnInit() {
  }

  ionViewDidEnter(){
    //this.storage.get('storage_xxx').then((res)=>{
      //console.log(res);
      //this.datastorage = res;
      //this.name = this.datastorage.your_name;
    //});
    this.start = 0;
    this.users = [];
    this.loadUsersadmin();
    
  }

  async loadUsersadmin(){
    return new Promise(resolve => {
      let body = {
        aksi: 'load_usersadmin',
        start: this.start,
        limit: this.limit
      }

      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        for(let datas of res.result){
          this.users.push(datas);
        }
        resolve(true);
      });
    });
  }



  async doRefreshadmin(event){
    const loader = await this.loadingCtrl.create({
      message: 'please wait....',
    });
    await loader.present();

    this.ionViewDidEnter();
    event.target.complete();

    loader.dismiss();
  }
  loadData(){
    this.start += this.limit;
    setTimeout(() => {
      this.loadUsersadmin().then(() =>{
        //event.target.complete();
      });
    }, 500);
  }


  async delDataadmin(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'del_usersadmin',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Administrador Eliminado');
          this.ionViewDidEnter();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }




}
